@import url(https://fonts.googleapis.com/css?family=Fira+Sans);
.ticket-container {
  width: min(60%, 1000px);
  background-color: hsl(240, 100%, 97%);
  border-radius: 5px;
  padding: 1rem;
  margin: 2rem auto;
  color: hsl(240, 40%, 60%);
  border: 3px solid hsl(240, 100%, 80%);
}

.ticket-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 2px solid hsl(240, 100%, 80%);
}

.ticket-header h2,
h6 {
  margin: 0.5em 0;
}

.ticket-status {
  display: -webkit-flex;
  display: flex;
}
.ticket-status > h5 {
  margin: 0.25em 0.35em;
  color: white;
  padding: 0.5em 0.75em;
  text-transform: uppercase;
  border-radius: 9px;
  cursor: pointer;
}

.ticket-status-1 {
  background-color: orange;
}
.ticket-status-2 {
  background-color: rgb(89, 89, 211);
}
.ticket-status-3 {
  background-color: rgb(129, 194, 31);
}

.ticket-description > p {
  max-width: 75ch;
}

.ticket-footer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.ticket-zones > ul {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  padding-left: 1em;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.ticket-zones > ul li {
  margin: 0.25em 0.35em;
  background-color: hsl(240, 100%, 80%);
  color: white;
  padding: 0.25em 0.75em;
  border-radius: 9999px;
}

.ticket-creation-date > h6 {
  font-size: 1rem;
  text-align: end;
  margin: 1em;
  width: 15ch;
}

.ticket-priority-1 {
  background-color: green;
}
.ticket-priority-2 {
  background-color: yellow;
}
.ticket-priority-3 {
  background-color: orange;
}
.ticket-priority-4 {
  background-color: red;
}

.ticket-type-1 {
  background-color: tomato;
}
.ticket-type-2 {
  background-color: slateblue;
}
.ticket-type-3 {
  background-color: palevioletred;
}


.comment-header {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.status-modal-container {
  position: fixed;
  inset: 0; /* not supported in edge, change for top/buttom/left/right */
  background-color: rgba(0, 0, 0, 0.7);
}

.status-modal {
  width: min(40%, 500px);
  height: 10rem;
  background-color: white;
  margin: 5rem auto;
}
.status-modal > h2 {
  text-align: center;
}

.status-list {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 0;
}

.status-list > li {
  color: #fff;
  width: 3.5rem;
  text-align: center;
  padding: 0.5rem;
  cursor: pointer;
  font-size: large;
  border-radius: 10%;
}

.status-list > li:nth-child(1) {
  background-color: orange;
  border: 0.2rem solid rgba(168, 112, 9, 0.397);
}
.status-list > li:nth-child(2) {
  background-color: rgba(0, 0, 255, 0.644);
  border: 0.2rem solid blue;
}
.status-list > li:nth-child(3) {
  background-color: rgba(128, 127, 127, 0.671);
  border: 0.2rem solid rgba(95, 94, 94, 0.671);
}

